import * as UI from './SellerAdmin.styled'
import { GoPlus } from 'react-icons/go'
import AppInput from '../../core/components/AppInput'
import { useInput } from '../../core/hooks/useInput'
import AppHeader from '../../core/components/AppHeader'
import AppButton from '../../core/components/AppButton'
import { useState } from 'react'
import { useGetCategoryQuery, usePostProductMutation } from '../../api/product/product.query'
import { useNavigate } from 'react-router-dom'
import { ImgWrap, TempImg } from '../userUpdate/UserUpdate.styled'

export default function SellerAdmin() {
  const navigate = useNavigate()

  const [profileImg, setProfileImg] = useState<any>()
  const [detailImgs, setDetailImgs] = useState<any>([])
  const profileImgHandler = (e: any) => {
    const files = e.target.files
    if (files.length <= 0) return console.log('files does not exist')
    const file = files[0]
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = () => {
      const base64 = reader.result
      setProfileImg(base64)
      return base64
    }
  }

  const profileImgListHandler = (e: any) => {
    let baseList: any = []
    const files = e.target.files
    if (files.length <= 0) return console.log('files does not exist')
    for (let i = 0; i < files.length; i++) {
      const reader = new FileReader()
      reader.readAsDataURL(files[i])
      reader.onload = () => {
        const base64 = reader.result
        baseList.push({ image_string: base64, file_name: `detail-${i}` })
      }
    }
    setDetailImgs(baseList)
  }

  const { value: price, onSetValue: setPrice } = useInput('')
  const { value: name, onSetValue: setName } = useInput('')
  const { value: stockQuantity, onSetValue: setStockQuantity } = useInput('')

  const categoryList = [
    { id: 4, name: '파운데이션' },
    { id: 5, name: '프라이머' },
    { id: 6, name: '블러셔' },
    { id: 7, name: '컨실러' },
    { id: 8, name: '블러쉬' },
    { id: 9, name: '루미나이저' },
    { id: 10, name: '파우더' },
    { id: 11, name: '실러' },
    { id: 13, name: '아이라이너' },
    { id: 14, name: '아이 섀도우' },
    { id: 15, name: '아이브로우' },
    { id: 16, name: '마스카라' },
    { id: 17, name: '립스틱' },
    { id: 18, name: '립라이너' },
    { id: 19, name: '립케어' },
  ]

  const [postProduct] = usePostProductMutation()
  const [category, setCategory] = useState(4)

  const onUploadProduct = async () => {
    const params: any = {
      category_id: category,
      name,
      price: Number(price),
      stock_quantity: stockQuantity,
      profile_image_string: profileImg,
      profile_file_name: 'main-product-img',
      detail_images: detailImgs,
    }
    postProduct(params)
      .unwrap()
      .then((res) => {
        window.alert('상품이 등록되었습니다.')
        navigate('/user')
      })
      .catch((err) => console.log(err))
  }

  return (
    <UI.Wrap>
      <AppHeader title="상품 추가 페이지" isBack />
      <UI.Select onChange={(e: any) => setCategory(e.target.value)}>
        {categoryList.map((d: any) => (
          <option key={d.id} onChange={() => setCategory(d.id)} value={d.id}>
            {d.name}
          </option>
        ))}
      </UI.Select>
      <UI.FormWrap>
        <AppInput label="가격" type="text" placeHolder="10,000" value={price} onSetValue={setPrice} />
        <AppInput label="상품 이름" placeHolder="검은 셔츠" value={name} onSetValue={setName} />
        <AppInput label="재고" placeHolder="100" value={stockQuantity} onSetValue={setStockQuantity} />
      </UI.FormWrap>
      <UI.FileSelectGroup>
        <ImgWrap style={{ alignItems: 'flex-end', gap: 0 }}>
          <UI.FileSelectWrap>
            <strong>기본 이미지 선택 ( 1개 )</strong>
            <UI.FileSelectBox>
              <input type="file" id="imageUrl" onChange={profileImgHandler} />
              <label htmlFor="imageUrl">
                <GoPlus />
              </label>
            </UI.FileSelectBox>
          </UI.FileSelectWrap>
          {profileImg && <TempImg src={`${profileImg}`} alt="update profile img" />}
        </ImgWrap>
        <ImgWrap style={{ alignItems: 'flex-end', gap: 0 }}>
          <UI.FileSelectWrap>
            <strong>이미지 선택 ( 최대 5개까지 가능합니다 )</strong>
            <UI.FileSelectBox>
              <input id="imageList" type="file" onChange={profileImgListHandler} multiple />
              <label htmlFor="imageList">
                <GoPlus />
              </label>
            </UI.FileSelectBox>
          </UI.FileSelectWrap>
          {detailImgs.length > 0 &&
            detailImgs.map((img: any) => <TempImg src={`${img.image_string}`} alt="update profile img" />)}
        </ImgWrap>
      </UI.FileSelectGroup>
      <AppButton
        content="상품 등록"
        onClick={onUploadProduct}
        radius="0.3rem"
        disabled={!price || !name || !stockQuantity}
      />
    </UI.Wrap>
  )
}
